<template>
  <div class="container">
    <div class="itemMusicList">
      <div class="top">
        <van-icon name="play-circle-o" size="25" /><span>播放全部</span>
      </div>
      <div class="itemList">
        <div class="item" v-for="(item, index) in itemList" :key="index">
          <div class="itemleft" @click="playMusic(index)">
            <span class="leftspan">{{ index + 1 }}</span>
            <div>
              <p>{{ item.name }}</p>
              <span
                v-for="(item, i) in item.ar"
                :key="i"
                style="font-size: 0.24rem"
                >{{ item.name }}</span
              >
            </div>
          </div>
          <div class="itemRight">
            <van-icon name="video-o" v-if="item.mv != 0" size="30" class="mv" />
            <van-icon name="ellipsis" size="30" />
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import Footer from '@/components/Footer.vue'
export default {
  props: ['itemList'],
  methods: {
    playMusic(index) {
      this.updataPlayList(this.itemList)
      this.updataPlayListIndex(index)
    },
    ...mapMutations(['updataPlayList', 'updataPlayListIndex'])
  },
  components: {
    Footer,
    Footer
  }
}
</script>

