<template>
  <div class="container">
    <itemMusicTop :playList="playList"></itemMusicTop>
    <div class="content">
      <img :src="playList.coverImgUrl" alt="" class="poster" />
      <div class="right">
        <div class="user-info">
          <div class="user-top">{{ playList.name }}</div>
          <div class="user-mid">
            <van-image
              round
              width="30px"
              height="30px"
              :src="creator.avatarUrl"
            />{{ creator.nickname }} <van-icon name="arrow" />
          </div>
        </div>
      </div>
      <div class="bottom">
        <div class="item">
          <van-icon name="comment-o" size="30" /><span>{{
            playList.commentCount
          }}</span>
        </div>
        <div class="item">
          <van-icon name="share-o" size="30" /><span>{{
            playList.shareCount
          }}</span>
        </div>
        <div class="item">
          <van-icon name="down" size="30" /><span>下载</span>
        </div>
        <div class="item">
          <van-icon name="more-o" size="30" /><span>多选</span>
        </div>
      </div>
    </div>
    <itemMusicList :itemList="itemList"></itemMusicList>
  </div>
</template>

<script>
import itemMusicTop from '@/components/item/itemMusicTop.vue'
import itemMusicList from '@/components/item/itemMusicList.vue'
import { getMusicItemList, getItemList } from '@/request/api/item.js'
export default {
  data() {
    return {
      playList: {},
      creator: {},
      itemList: []
    }
  },
  mounted() {
    this.getData(), this.getItemList()
  },
  methods: {
    async getData() {
      let res = await getMusicItemList(this.$route.query.id)
      // console.log(res)
      this.playList = res.data.playlist
      // console.log(this.playList)
      this.creator = res.data.playlist.creator
    },
    // 每一首歌
    async getItemList() {
      let res = await getItemList(this.$route.query.id)
      // console.log(res)
      this.itemList = res.data.songs
      // console.log(this.itemList)
    }
  },
  components: {
    itemMusicTop,
    itemMusicList
  }
}
</script>

<style lang="less" scoped>
.container {
  position: relative;
  .content {
    display: flex;
    .poster {
      position: absolute;
      top: 1.5rem;
      left: 0.3rem;
      width: 3rem;
      height: 3rem;
      border-radius: 0.4rem;
    }
    .right {
      position: absolute;
      top: 1.5rem;
      left: 3.6rem;
      right: 0.2rem;
      .user-info {
        .user-top {
          color: #fff;
        }
        .user-mid {
          margin-top: 0.4rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          color: #fff;
        }
      }
    }
    .bottom {
      position: absolute;
      top: 5rem;
      width: 100%;
      height: 1rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .item {
        width: 250%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: #fff;
      }
    }
  }
}
</style>
