<template>
  <div class="container">
    <img :src="musicList.al.picUrl" alt="" class="bgimg" />
    <div class="detailTop">
      <div class="left">
        <van-icon name="arrow-left" size="30" @click="backHome" />
        <div class="userinfo">
          <p>{{ musicList.name }}</p>
          <span v-for="(item, i) in musicList.ar" :key="i">{{
            item.name
          }}</span>
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="right"><van-icon name="share-o" size="30" /></div>
    </div>
    <div class="content" v-show="!isLyricShow">
      <img src="@/assets/circle.png" alt="" class="circle" />
      <img
        src="@/assets/needle-ab.png"
        alt=""
        class="needle"
        :class="{ needle_active: !isbtnShow }"
      />
      <img
        :src="musicList.al.picUrl"
        alt=""
        @click="isLyricShow = true"
        class="img_al"
        :class="{ img_al_active: !isbtnShow, img_al_pauesd: isbtnShow }"
      />
    </div>
    <div
      class="lyric"
      ref="musicLyric"
      v-show="isLyricShow"
      @click="isLyricShow = false"
    >
      <p
        v-for="(item, i) in lyric"
        :key="i"
        :class="{
          active:
            currentTime * 1000 >= item.time && currentTime * 1000 < item.pre
        }"
      >
        {{ item.lrc }}
      </p>
    </div>
    <div class="footer">
      <div class="ft">
        <van-icon name="like-o" size="30" />
        <van-icon name="down" size="30" />
        <van-icon name="music-o" size="30" />
        <van-icon name="chat-o" size="30" />
        <van-icon name="orders-o" size="30" />
      </div>
      <div class="fm">
        <input
          type="range"
          min="0"
          :max="duration"
          class="range"
          v-model="currentTime"
        />
      </div>
      <div class="fb">
        <van-icon name="replay" size="35" />
        <van-icon name="arrow-left" size="35" @click="goPlay(-1)" />
        <van-icon
          name="play-circle-o"
          size="35"
          @click="play"
          v-if="isbtnShow"
        />
        <van-icon name="pause-circle-o" size="35" @click="play" v-else />
        <van-icon name="arrow" size="35" @click="goPlay(1)" />
        <van-icon name="bars" size="35" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
export default {
  data() {
    return {
      isLyricShow: false
    }
  },
  computed: {
    ...mapState([
      'lyricList',
      'currentTime',
      'playListIndex',
      'playList',
      'duration'
    ]),
    // 歌词处理
    lyric() {
      let arr
      if (this.lyricList.lyric) {
        arr = this.lyricList.lyric.split(/[(\r\n)\r\n]+/).map((item, i) => {
          // 分
          let min = item.slice(1, 3)
          // 秒
          let sec = item.slice(4, 6)
          // 毫秒
          let mill = item.slice(7, 10)
          // 歌词
          let lrc = item.slice(11, item.length)
          // 总时长
          let time =
            parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill)

          // console.log(min, sec, mill, lrc)
          return { min, sec, mill, lrc, time }
        })
        arr.forEach((item, i) => {
          if (i === arr.length - 1 || isNaN(arr[i + 1].time)) {
            item.pre = 10000
          } else {
            item.pre = arr[i + 1].time
          }
        })
      }

      // console.log(arr)
      return arr
    }
  },
  mounted() {
    this.addDuration()
  },
  props: ['musicList', 'isbtnShow', 'play', 'addDuration'],
  methods: {
    ...mapMutations(['updataDetailShow', 'updataPlayListIndex']),
    backHome() {
      this.updataDetailShow()
      this.isLyricShow = false
    },
    goPlay(num) {
      let index = this.playListIndex + num
      if (index < 0) {
        index = this.playList.length - 1
      } else if (index == this.playList.length) {
        index = 0
      }
      this.updataPlayListIndex(index)
    }
  },
  watch: {
    currentTime(newVal) {
      let p = document.querySelector('p.active')
      if (p) {
        if (p.offsetTop > 200) {
          this.$refs.musicLyric.scrollTop = p.offsetTop - 200
        }
      }
      if (newVal === this.duration) {
        if (this.playListIndex === this.playList.length - 1) {
          this.updataPlayListIndex(0)
          this.play()
        } else {
          this.updataPlayListIndex(this.playListIndex + 1)
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .bgimg {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    filter: blur(1rem);
  }
  .detailTop {
    width: 100%;
    height: 1rem;
    display: flex;
    padding: 0.2rem;
    justify-content: space-between;
    align-items: center;
    color: black;
    .left {
      display: flex;
      align-items: center;
      .userinfo {
        width: 4rem;
        height: 100%;
        margin-left: 0.4rem;
      }
    }
  }
  .lyric {
    width: 100%;
    height: 8.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    overflow: scroll;
    margin-top: 0.3rem;
    p {
      color: black;
      margin-bottom: 0.4rem;
    }
    .active {
      color: #fff;
      font-size: 0.4rem;
    }
  }
  .content {
    width: 100%;
    height: 9rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    .needle {
      width: 2rem;
      height: 3rem;
      position: absolute;
      left: 46%;
      transform-origin: 0 0;
      transform: rotate(-13deg);
      transition: all 2s;
    }
    .needle_active {
      width: 2rem;
      height: 3rem;
      position: absolute;
      left: 46%;
      transform-origin: 0 0;
      transform: rotate(5deg);
      transition: all 2s;
    }
    .circle {
      width: 250px;
      height: 250px;
      position: absolute;
      bottom: 100px;
      z-index: -1;
    }
    .img_al {
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
      position: absolute;
      bottom: 3rem;
      animation: rotate_al 10s linear infinite;
    }
    .img_al_active {
      animation-play-state: running;
    }
    .img_al_pauesd {
      animation-play-state: paused;
    }
    @keyframes rotate_al {
      0% {
        transform: rotateZ(0deg);
      }
      100% {
        transform: rotateZ(360deg);
      }
    }
  }
  .footer {
    position: absolute;
    bottom: 0.2rem;
    left: 0;
    width: 100%;
    height: 3rem;
    .ft {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0.4rem 0.4rem;
    }
    .fm {
      display: fixed;
      width: 100%;
      .range {
        width: 100%;
        height: 0.06rem;
      }
    }
    .fb {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0.3rem 0.4rem 0;
    }
  }
}
</style>
