import service from '..'
/* 二维码登录 */
// 获取二维码key
export function getQrKey() {
  return service({
    method: 'GET',
    url: '/login/qr/key',
    params: {
      timestamp: Date.now()
    }
  })
}
// 生成二维码Base 64
export function createQr(key, qrimg = true) {
  return service({
    method: 'GET',
    url: `/login/qr/create`,
    params: {
      key,
      qrimg,
      timestamp: Date.now()
    }
  })
}
// 轮询二维码状态 800 为二维码过期,801 为等待扫码,802 为待确认,803 为授权登录成功(803 状态码下会返回 cookies)
export function checkQr(key) {
  return service({
    method: 'GET',
    url: `/login/qr/check`,
    params: {
      key,
      timestamp: Date.now()
    }
  })
}
/* 获取账号信息 */
export function getAcount() {
  return service({
    method: 'GET',
    url: '/user/account',
    params: {
      timestamp: Date.now()
    }
  })
}
/* 获取用户歌单 */
export function getUserPlayList(uid, offset, limit = 30) {
  return service({
    method: 'GET',
    url: '/user/playlist',
    params: {
      uid,
      offset,
      limit,
      timestamp: Date.now()
    }
  })
}
