<template>
  <div class="login">
    <div class="login-container">
      <div>
        <h2 style="text-align: center">二维码登录</h2>
        <div class="img-wrap">
          <img class="img img-h" :src="imgData" />
          <div class="btn-refresh pointer" v-show="qrType === 800">
            <i class="el-icon-refresh-left"></i>
          </div>
          <div class="msg-wrap" style="text-align: center">
            状态：{{ message }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getQrKey, createQr, checkQr } from '@/request/api/user.js'
export default {
  data() {
    return {
      key: '',
      imgData: '',
      qrType: '',
      message: ''
    }
  },
  methods: {
    /* 获取二维码key */
    async getKey() {
      const res = await getQrKey()
      // console.log(res)
      this.key = res.data.data.unikey
      this.createQr()
    },
    /* 生成二维码 */
    async createQr() {
      const res = await createQr(this.key)
      // console.log(res)
      this.imgData = res.data.data.qrimg
      this.checkQr()
    },
    // /* 800 为二维码过期,801 为等待扫码,802 为待确认,803 为授权登录成功(803 状态码下会返回 cookies) */
    // /* 检查二维码状态 */
    async checkQr() {
      const res = await checkQr(this.key)
      // console.log(res)
      this.qrType = res.data.code
      this.message = res.data.message
      if (res.data.code === 801 || res.data.code === 802) {
        this.timer = window.setTimeout(() => {
          this.checkQr()
        }, 5000)
      } else if (res.data.code === 803) {
        this.$router.push('/')
      }
    }
  },
  created() {
    this.getKey()
  }
}
</script>

<style lang="less" scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 200px;
}
</style>
