import Vue from 'vue'
import Vuex from 'vuex'
import { getMusicLyric } from '@/request/api/item.js'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 播放列表
    playList: [
      {
        al: {
          id: 35644242,
          name: '夏至未至 影视原声带',
          pic: 19149094509535910,
          picUrl:
            'https://p1.music.126.net/ZZAQGWl9mR7g5xCyWWH3Pw==/19149094509535913.jpg',
          pic_str: '19149094509535913'
        },
        id: 483671599,
        ar: [
          {
            name: '岑宁儿'
          }
        ],
        name: '追光者'
      }
    ],
    playListIndex: 0,
    // 为真即为暂停，显示播放按钮
    isbtnShow: true,
    // 歌曲详情页
    detailShow: false,
    // 歌词
    lyricList: {},
    currentTime: 0,
    // 歌曲总时长
    duration: 0,
    isFooterMusic: true,
    /* 登录态 */
    isLogin: false
  },
  getters: {},
  mutations: {
    updataIsbtnShow(state, value) {
      state.isbtnShow = value
    },
    updataPlayList(state, value) {
      state.playList = value
    },
    updataPlayListIndex(state, value) {
      state.playListIndex = value
    },
    updataDetailShow(state) {
      state.detailShow = !state.detailShow
    },
    updataLyricList(state, value) {
      state.lyricList = value
    },
    updataCurrentTime(state, value) {
      state.currentTime = value
    },
    updataDuration(state, value) {
      state.duration = value
    }
  },
  actions: {
    // 获取数据为异步请求
    async getLyric(context, value) {
      let res = await getMusicLyric(value)
      // console.log(res)
      context.commit('updataLyricList', res.data.lrc)
    }
  },
  modules: {}
})
