<template>
  <div class="Footer">
    <div class="left" @click="updataDetailShow">
      <img :src="playList[playListIndex].al.picUrl" alt="" />
      <div>
        <p class="title">{{ playList[playListIndex].name }}</p>
        <span>横滑切换上下首</span>
      </div>
    </div>
    <div class="right">
      <van-icon name="play-circle-o" size="30" @click="play" v-if="isbtnShow" />
      <van-icon name="pause-circle-o" size="30" @click="play" v-else />
      <van-icon name="bars" size="30" />
    </div>
    <audio
      ref="audio"
      :src="`https://music.163.com/song/media/outer/url?id=${playList[playListIndex].id}.mp3`"
    ></audio>
    <!-- 弹出 -->
    <van-popup
      v-model="modelName"
      position="right"
      :style="{ height: '100%', width: '100%' }"
    >
      <MusicDetail
        :musicList="playList[playListIndex]"
        :play="play"
        :isbtnShow="isbtnShow"
        :addDuration="addDuration"
      ></MusicDetail>
    </van-popup>
  </div>
</template>

<script>
import MusicDetail from '@/components/item/musicDetail.vue'
import { mapState, mapMutations } from 'vuex'
export default {
  data() {
    return {
      modelName: false,
      interVal: 0
    }
  },
  mounted() {
    this.$store.dispatch('getLyric', this.playList[this.playListIndex].id)
    this.updateTime()
  },
  updated() {
    this.$store.dispatch('getLyric', this.playList[this.playListIndex].id)
    this.addDuration()
  },
  computed: {
    ...mapState(['playList', 'playListIndex', 'isbtnShow', 'detailShow'])
  },
  components: { MusicDetail },
  methods: {
    play() {
      // 先判断音乐是否播放
      if (this.$refs.audio.paused) {
        this.$refs.audio.play()
        this.updataIsbtnShow(false)
        // 播放时调用
        this.updateTime()
      } else {
        this.$refs.audio.pause()
        this.updataIsbtnShow(true)
        // 暂停时清除
        clearInterval(this.interVal)
      }
    },
    updateTime() {
      this.interVal = setInterval(() => {
        this.updataCurrentTime(this.$refs.audio.currentTime)
      }, 500)
    },
    // 加总时长
    addDuration() {
      this.updataDuration(this.$refs.audio.duration)
    },
    ...mapMutations([
      'updataIsbtnShow',
      'updataDetailShow',
      'updataCurrentTime',
      'updataDuration'
    ])
  },
  watch: {
    playListIndex() {
      this.$refs.audio.autoplay = true
      if (this.$refs.audio.paused) {
        this.updataIsbtnShow(false)
      }
    },
    playList() {
      if (this.isbtnShow) {
        this.$refs.audio.autoplay = true
        this.updataIsbtnShow(false)
      }
    },
    detailShow(val) {
      this.modelName = val
    }
  }
}
</script>

<style lang="less" scoped>
.Footer {
  width: 100%;
  height: 1.4rem;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  border-top: 1px solid #999;
  padding: 0.2rem;
  display: flex;
  justify-content: space-between;
  .left {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
    .title {
      margin-bottom: 0.1rem;
    }
  }
  .right {
    width: 20%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
</style>
