<template>
  <!-- 推荐歌单 -->
  <div class="music-recom">
    <div class="music-top">
      <div class="title">推荐歌单</div>
      <div class="more">更多<van-icon name="arrow" /></div>
    </div>
    <div class="musicList">
      <van-swipe
        :loop="false"
        :width="150"
        class="myswipe"
        :show-indicators="false"
      >
        <!-- 循环每一个歌单 -->
        <van-swipe-item v-for="(item, index) in RecomList" :key="index">
          <router-link :to="{ path: '/itemmusic', query: { id: item.id } }">
            <img :src="item.picUrl" alt="" />
            <span class="palyCount"
              ><van-icon name="play-circle-o" />{{
                changeCount(item.playCount)
              }}</span
            >
            <span class="title">{{ item.name }}</span>
          </router-link>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
import { getRecomList } from '@/request/api/home'
export default {
  data() {
    return {
      RecomList: []
    }
  },
  created() {
    this.getRecom()
  },
  methods: {
    // 推荐歌单
    async getRecom() {
      let res = await getRecomList()
      // console.log(res)
      this.RecomList = res.data.result
    },
    // 播放数改单位
    changeCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + '亿'
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + '万'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.music-recom {
  .music-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0.2rem;
    .title {
      font-size: 0.36rem;
      font-weight: bold;
    }
    .more {
      padding: 0.06rem 0.06rem 0.06rem 0.1rem;
      border: 0.02rem solid #ccc;
      border-radius: 0.4rem;
    }
  }
  .musicList {
    width: auto;
    height: 5rem;
    .myswipe {
      height: 100%;
      .van-swipe-item {
        box-sizing: border-box;
        padding: 0.1rem;
        img {
          width: 100%;
          height: 3rem;
          border-radius: 0.2rem;
        }
        .title {
          font-size: 0.28rem;
        }
        .palyCount {
          position: absolute;
          top: 0.2rem;
          right: 0.2rem;
          color: #fff;
        }
      }
    }
  }
}
</style>
